outer-link>\n        </div>\n      </div>\n      <el-table :data=\"list\">\n        <!-- <el-table-column label=\"图片\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <img :src=\"scope.row.photo\"\n                 class=\"img\">\n          </template>\n        </el-table-column> -->\n        <el-table-column align=\"center\"\n                         prop=\"company\"\n                         label=\"名称\">\n        </el-table-column>\n        <el-table-column label=\"类型\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            {{scope.row.type==1 ? '五折养护' : '单次免费'}}\n          </template>\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         prop=\"create_time\"\n                         label=\"创建时间\">\n        </el-table-column>\n        <el-table-column label=\"操作/是否显示\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-switch :active-value='1'\n                       :inactive-value='2'\n                       active-color=\"#13ce66\"\n                       inactive-color=\"#ff4949\"\n                       v-model=\"scope.row.status\"\n                       @change='Setup(scope.row)'>\n            </el-switch>\n            <el-button type=\"primary\"\n                       size=\"mini\"\n                       style=\"margin-left:10px\"\n                       @click=\"addVisible=true,state=2,defaultData(scope.row)\">修改</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <!-- 分页 -->\n      <!-- <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list && list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n      </div> -->\n    </div>\n    <el-dialog center\n               :title=\"state==1 ? '添加': '修改'\"\n               :visible.sync=\"addVisible\"\n               @close='()=>{\n                   $refs.form.resetFields()\n               }'>\n      <el-form label-width=\"100px\"\n               :model=\"listQuery\"\n               :rules=\"rules\"\n               ref=\"form\">\n        <el-form-item label=\"名称:\"\n                      prop=\"company\">\n          <el-input placeholder=\"请输出名称\"\n                    class=\"input\"\n                    v-model=\"listQuery.company\" />\n        </el-form-item>\n        <el-form-item label=\"类型:\"\n                      prop=\"type\">\n          <el-radio v-model=\"listQuery.type\"\n                    :label=\"1\">五折养护</el-radio>\n          <el-radio v-model=\"listQuery.type\"\n                    :label=\"2\">单次免费</el-radio>\n        </el-form-item>\n        <!-- <el-form-item label=\"图片:\"\n                      prop=\"photo\">\n          <el-upload :action=\"`${uploadUrl}admin/SystemSetup/uploadPic`\"\n                     list-type=\"picture-card\"\n                     :on-success='handleSuccess'\n                     :data=\"{token:token}\"\n                     :file-list=\"listQuery.photo  ?  [{url:listQuery.photo}] : []\"\n                     :limit=\"1\"\n                     name=\"image\">\n            <i class=\"el-icon-plus\"></i>\n          </el-upload>\n        </el-form-item> -->\n        <el-form-item>\n          <el-button type=\"primary\"\n                     @click=\"state==1? add() : Modify()\"\n                     :loading=\"loading\">{{state==1 ? '添加' : '修改'}}</el-button>\n        </el-form-item>\n      </el-form>\n    </el-dialog>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 0,\n      pageCount: 0,\n      authList: [],\n      threeAuthList: [],\n      uploadUrl: this.baseURL,\n      addVisible: false,\n      loading: false,\n      listQuery: {\n        company: '',\n        type: 1\n      },\n      state: 1, // 1 添加 2 修改\n      rules: {\n        company: { required: true, message: '名称不能为空', trigger: 'blur' },\n        type: { required: true, message: '请选择类型', trigger: 'blur' },\n        // photo: { required: true, message: '请上传图片', trigger: 'blur' },\n      }\n    }\n  },\n  created () {\n    this.init();\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/SystemSetup/systemList', { token: this.token, page: this.page, types: 2 })\n        .then(res => {\n          this.list = res.data.data.list;\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n    //设置 显示 隐藏\n    async Setup (row) {\n      try {\n        const res = await this.$axios.post('admin/SystemSetup/updateStatus', { token: this.token, id: row.id, status: row.status })\n        if (res.data.code == 1) {\n          this.$message({ message: res.data.msg, type: 'success' })\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (error) {\n        throw (error)\n      }\n    },\n    //修改\n    Modify () {\n      this.$refs.form.validate(async (valid) => {\n        if (valid) {\n          try {\n            this.loading = true\n            const res = await this.$axios.post('admin/SystemSetup/updateInfo', Object.assign({}, this.listQuery, { token: this.token, types: 2 }))\n            this.loading = false\n            if (res.data.code == 1) {\n              this.$message({ message: res.data.msg, type: 'success' })\n              this.addVisible = false\n              this.init()\n            } else {\n              this.$message.error(res.data.msg)\n            }\n          } catch (error) {\n            this.loading = false\n            throw (error)\n          }\n        }\n      })\n\n    },\n    defaultData (row) {  //默认数据\n      this.$nextTick(() => {\n        this.listQuery = Object.assign({}, row)\n      })\n    },\n    add () {  //添加\n      this.$refs.form.validate(async (valid) => {\n        if (valid) {\n          try {\n            this.loading = true\n            const res = await this.$axios.post('admin/SystemSetup/addGenre', Object.assign({}, this.listQuery, { token: this.token, types: 2 }))\n            this.loading = false\n            if (res.data.code == 1) {\n              this.$message({ message: res.data.msg, type: 'success' })\n              this.addVisible = false\n              this.init()\n            } else {\n              this.$message.error(res.data.msg)\n            }\n          } catch (error) {\n            this.loading = false\n            throw (error)\n          }\n        }\n      })\n    },\n    handleSuccess (res) {  //上传成功\n      this.listQuery = Object.assign({}, this.listQuery, { photo: res })\n    }\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '渠道客户服务') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '主机厂' && arr[i].name == '渠道客户服务') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n}\n</script>\n<style scoped>\n.ellipsis {\n  width: 150px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n}\n.img {\n  width: 70px;\n  height: 70px;\n}\n.input {\n  width: 50%;\n}\n.laybox2 ul {\n  margin: 33px auto;\n  border-bottom: 1px dashed #04bbfc;\n}\n\n.laybox2 {\n  padding: 60px;\n  font-size: 16px;\n}\n\n.laybox2 li {\n  width: 300px;\n  text-align: left;\n  margin: 33px auto;\n}\n\n.t-c:after {\n  content: \"\";\n  display: block;\n  height: 0;\n  clear: both;\n}\n\n.bot > div {\n  position: relative;\n}\n\ndiv.bot {\n  margin: 45px auto 35px auto;\n  height: 100px;\n}\n\ndiv.bot span {\n  display: inline-block;\n  width: 20%;\n}\n\ndiv.bot input {\n  display: inline-block;\n  width: 55%;\n  outline: none;\n  border: none;\n  border-bottom: 1px solid #aaa;\n}\n\ndiv.bot button {\n  width: 110px;\n  height: 36px;\n  margin-left: -55px;\n  position: absolute;\n  border-radius: 18px;\n  box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n  border: none;\n  color: #fff;\n  left: 50%;\n  top: 60px;\n}\n\nbutton.reject {\n  background: rgba(237, 81, 55, 1);\n  margin-right: 30%;\n}\n\nbutton.pass {\n  background-color: #20a0ff;\n}\n\n.left {\n  float: left;\n  width: 50%;\n}\n\n.right {\n  float: right;\n  width: 50%;\n}\n.nowrap {\n  width: 120px !important;\n  text-align: center;\n  white-space: nowrap; /*规定文本不换行**/\n  text-overflow: ellipsis; /**显示省略符号来代表被修剪的文本。*/\n  overflow: hidden;\n  margin: 0 auto;\n}\n.regionDetail >>> .el-dialog__title {\n  font-size: 22px;\n}\n.regionDetail >>> .el-dialog__body {\n  padding: 0px 25px 30px;\n  border-top: solid 2px #eee;\n}\n</style>\n"],"sourceRoot":""}]);

